@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
header {
  font-style: italic;
  padding: 1.5em 0;
  color: white;
  background: linear-gradient(to right, #fb913c, #ef4444, #ec4899);
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0.1);
}

header h1 {
  text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.1);
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0 auto;
}

.scores {
  padding: 1em 0;
}

@media (min-width: 450px) {
  header h1 {
    text-align: left;
    margin: 0 auto 0 0;
  }

  .scores {
    text-align: right;
    padding: 0;
  }
}

.instructions {
  background-color: rgba(255, 0, 0, 0.1);
  color: rgba(158, 0, 0, 1);
  margin-bottom: 1em;
  padding: 0.5em 0;
}

.card {
  background-color: white;
  margin: 1em;
  border-radius: 10px;
  box-shadow: 0 0 10px 0 rgb(0, 0, 0, 0.1);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.1s ease-in;
  cursor: pointer;
}

.card:hover {
  transform: scale(1.1);
}

.card img {
  width: 200px;
}

.card .img-container {
  margin: auto 0;
}

.card p {
  text-align: center;
  width: 180px;
  margin: 0 auto;
  padding: 0.5em 0;
  font-style: italic;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.card-board {
  justify-content: center;
}

* {
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: rgba(0, 0, 0, 0.05);
}

.container {
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-center {
  text-align: center;
}

@media (min-width: 450px) {
  .container {
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 2em;
  }
}

@media (min-width: 800px) {
  .container {
    margin: 0 auto;
  }
}

